
import { defineComponent, ref } from "vue";
import { hero, mission, onMission } from "@/interface";
import { formatDate } from "./mixins/formatDateMixin";
import jsonToObject from "./mixins/forLoopMixin";
import fetchPost from "./mixins/fetchMixin";
import { CRUD } from "@/interface";
import router from "./router";
import {
  faArrowLeft,
  faEllipsis,
  faPenNib,
  faTrashCan,
  faPlus,
} from "@fortawesome/free-solid-svg-icons";
import { library } from "@fortawesome/fontawesome-svg-core";

import { getCurrentInstance } from "vue";

library.add(faArrowLeft, faEllipsis, faPenNib, faTrashCan, faPlus);

export default defineComponent({
  setup() {
    const { proxy } = getCurrentInstance() as any;
    const proxyData = ref({
      url: proxy.$getUrl,
      fetchCode: proxy.$fetchCode,
    });
    let missions = ref<mission[]>([]);
    let completedMissions = ref<mission[]>([]);
    let hero = ref<hero>({ id: 1, exp: 0 });

    fetch(`${proxyData.value.url}&sheet=Missions`)
      .then((res) => res.text())
      .then((rep) => (missions.value = jsonToObject(rep)));

    fetch(
      `${proxyData.value.url}&sheet=CompletedMissions&tq=${encodeURIComponent(
        "select * order by A desc"
      )}`
    )
      .then((res) => res.text())
      .then((rep) => (completedMissions.value = jsonToObject(rep)));

    fetch(`${proxyData.value.url}&sheet=Hero`)
      .then((res) => res.text())
      .then((rep) => (hero.value.exp = jsonToObject(rep)[0].exp));

    return { missions, completedMissions, hero, proxyData };
  },
  data() {
    return {
      onMission: {} as mission,
      firstLevelMaxExp: 10,
      levelIncreaseRate: 1.2,
    };
  },
  methods: {
    createMission(mission: mission): void {
      mission.id = Math.max(...this.missions.map((m) => m.id)) + 1;
      fetchPost<mission>(
        CRUD.create,
        "Missions",
        this.proxyData.fetchCode,
        mission
      );
      this.missions.push(mission);
    },
    editMission(mission: mission): void {
      fetchPost<mission>(
        CRUD.update,
        "Missions",
        this.proxyData.fetchCode,
        mission
      );
      const index = this.missions.findIndex((m) => m.id === mission.id);
      this.missions[index] = mission;
    },
    removeMission(mission: mission): void {
      fetchPost<mission>(
        CRUD.delete,
        "Missions",
        this.proxyData.fetchCode,
        mission
      );
      const index = this.missions.findIndex((m) => m.id === mission.id);
      this.missions.splice(index, 1);
      router.push({ name: "home" });
    },
    startMission(onMission: onMission): void {
      fetchPost<onMission>(
        CRUD.update,
        "OnMission",
        this.proxyData.fetchCode,
        onMission
      );
    },
    completeMission(mission: mission): void {
      mission.id =
        this.completedMissions.length === 0
          ? 1
          : Math.max(...this.completedMissions.map((m) => m.id)) + 1;

      const missionMilisecond = mission.minute * 60 * 1000;
      let dateInTime = new Date().getTime() + missionMilisecond;
      mission.completedDate = formatDate(new Date(dateInTime));
      delete mission.description;
      console.log(mission);
      fetchPost<mission>(
        CRUD.create,
        "CompletedMissions",
        this.proxyData.fetchCode,
        mission
      );
      this.completedMissions.unshift(mission);

      fetchPost<onMission>(CRUD.update, "OnMission", this.proxyData.fetchCode, {
        id: 1,
        onMissionCompletedDate: "",
        onMissionId: 0,
      });

      this.hero.exp += mission.point;
      fetchPost<hero>(CRUD.update, "Hero", this.proxyData.fetchCode, this.hero);
    },
  },
});
