export interface mission {
    id: number;
    title: string;
    minute: number;
    point: number;
    completedDate: string;
    description?: string;
}

export interface hero {
    id: number;
    exp: number;
}

export interface onMission {
    id: number;
    onMissionId: number | null;
    onMissionCompletedDate: string | null;
}

export enum CRUD {
    create = 'create',
    reading = 'reading',
    update = 'update',
    delete = 'delete'
}